*{margin:0;padding:0;box-sizing:border-box; list-style-type: none;}
html{font-size:10px;}
body{font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; height:100vh; display:flex; justify-content: center; align-items: center; background-color: midnightblue;}

.counterContainer{width:40rem; margin:auto; background-color: aquamarine; padding:2rem 4rem; border-radius: 1rem; display:flex; flex-direction: column; gap:2rem; align-items: center;}

.count{width:4rem; height: 4rem; border-radius: 50%; border:1px solid rgb(142, 90, 211); padding:4rem; display:flex;justify-content: center; align-items: center; font-size:2.5rem; background-color: indigo;}

.buttons{display:flex; justify-content:center; align-items: center; gap:1rem}

.btn{padding:0.5rem 2rem; border-radius: 0.5rem; font-size:1.6rem; font-weight:500; border:none; box-shadow:0 0 2px 0 rgba(0,0,0,0.5); cursor:pointer}
.red{background-color: brown; color: aliceblue;}
.yellow{background-color: yellow;}